    .progress-title{
        margin-top: 5%;
        color: black
    }
    .progress{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 180px;
        height: 180px;
        background-color: #bee792 ;
        border-radius: 50%;
        margin-top: 20px;
        color: black;
        font-size: 50px;
        font-weight: bolder;
        position: relative;
        overflow: visible;

        &::after {
            content: "";
            position: absolute;
            top: -10px;
            left: -10px;
            right: -10px;
            bottom: -10px;
            border-radius: 50%;
            border: 8px solid transparent;
            border-top-color: #005CA3;
            animation: spin 1.5s linear infinite;
        }
    }

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
        